import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { MatSnackBar } from '@angular/material';
import { RoleService } from "../role.service";
import { Role, RolesMessage } from "../role";
import { RoleComponent } from '../role/role.component';

@Component({
  selector: 'dmz-role-list',
  templateUrl: './role-list.component.html',
  styleUrls: ['./role-list.component.css']
})
export class RoleListComponent implements OnInit {

  Roles: Role[] = [];
  tableColumns: string[] = ['RoleID', 'Name', 'RemoteAccess', 'InternalTTL', 'RemoteTTL'];
  selectedRow = 0;
  selectedRowNumber = 0;
  dialogConfig = new MatDialogConfig();
  
  constructor(private roleService: RoleService,
    private snackBar: MatSnackBar,
    private location: Location,
    private dialog: MatDialog) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
    this.dialogConfig.autoFocus = true;
    this.dialogConfig.width = "75%";

    this.roleService.getRoles().subscribe((RolesMsg: RolesMessage) => {
      this.Roles = RolesMsg.Roles;
      this.snackBar.open(RolesMsg.Count + ' Roles loaded. ', '', { duration: 3000 });
      console.log(this.Roles);
      return this.Roles;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Roles found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }

  onNew() {
    this.dialogConfig.data = undefined;
    this.dialog.open(RoleComponent, this.dialogConfig);
  }

  onEdit() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(RoleComponent, this.dialogConfig);
  }

  onDelete() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(RoleComponent, this.dialogConfig);
  }
}
