import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { DeviceService } from "../device.service";
import { Device, DevicesMessage } from "../device";
import { DeviceComponent } from '../device/device.component';

@Component({
  selector: 'dmz-device-list',
  templateUrl: './device-list.component.html',
  styleUrls: ['./device-list.component.css']
})
export class DeviceListComponent implements OnInit {

  Devices: Device[] = [];
  tableColumns: string[] = ['DeviceID', 'InterfaceID', 'Name', 'ExternalID','Timestamp'];
  selectedRow = 0;
  selectedRowNumber = 0;
  dialogConfig = new MatDialogConfig();

  constructor(private deviceService: DeviceService,
    private snackBar: MatSnackBar,
    private location: Location,
    private route: ActivatedRoute,
    private dialog: MatDialog) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
    this.dialogConfig.autoFocus = true;
    this.dialogConfig.width = "75%";

    this.deviceService.getDevices(this.route.snapshot.params['id']).subscribe((DevicesMsg: DevicesMessage) => {
      this.Devices = DevicesMsg.Devices;
      this.snackBar.open(DevicesMsg.Count + ' Devices loaded. ', '', { duration: 3000 });
      console.log(this.Devices);
      return this.Devices;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Devices found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }

  onNew() {
    this.dialogConfig.data = undefined;
    this.dialog.open(DeviceComponent, this.dialogConfig);
  }

  onEdit() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(DeviceComponent, this.dialogConfig);
  }

  onDelete() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(DeviceComponent, this.dialogConfig);
  }
}
