import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { ValueService } from "src/app/detail/value.service";
import { Value, ValuesMessage } from "src/app/detail/value";
import { ValueComponent } from 'src/app/detail/value/value.component';

@Component({
  selector: 'dmz-value-list',
  templateUrl: './value-list.component.html',
  styleUrls: ['./value-list.component.css']
})
export class ValueListComponent implements OnInit {

  Values: Value[] = [];
  tableColumns: string[] = ['ValueID','Name','DeviceID','UnitID','Value','RetentionDays','RetentionInterval','Timestamp'];
  selectedRow = 0;
  selectedRowNumber = 0;
  dialogConfig = new MatDialogConfig();

  constructor(private valueService: ValueService,
    private snackBar: MatSnackBar,
    private location: Location,
    private route: ActivatedRoute,
    private dialog: MatDialog) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
    this.dialogConfig.autoFocus = true;
    this.dialogConfig.width = "75%";

    this.valueService.getValues(this.route.snapshot.params['id']).subscribe((ValuesMsg: ValuesMessage) => {
      this.Values = ValuesMsg.Values;
      this.snackBar.open(ValuesMsg.Count + ' Values loaded. ', '', { duration: 3000 });
      console.log(this.Values);
      return this.Values;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Values found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }

  onNew() {
    this.dialogConfig.data = undefined;
    this.dialog.open(ValueComponent, this.dialogConfig);
  }

  onEdit() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(ValueComponent, this.dialogConfig);
  }

  onDelete() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(ValueComponent, this.dialogConfig);
  }
}
